.gameboard {
  display: block;
  height: 100%;
  overflow-y: auto;
}

@media screen and (max-width: 600px) {
  .gameboard {
    padding-bottom: 50px;
  }  
}

.gameboard ol {
  list-style: none;
  padding: 0;
  --hue: 1;
  --unit: 1rem;
}
.gameboard li {
  --height: 6rem;
  position: relative;
  display: block;
  background-color: hsl(calc(var(--hue)*360/20),90%,35%);
  border-color: hsl(calc(var(--hue)*360/20),90%,35%);
  padding: 0.75rem;
  margin: 2rem 1rem;

}
.gameboard li::before {
  content: "";
  background-color: inherit;
  position: absolute;
  display: block;
  width: var(--unit);
  height: var(--unit);
  top: 100%;
  left: calc(50% - (var(--unit)/2));
}
.gameboard li::after {
  content: "";
  position: absolute;
  display: block;
  top: calc(100% + var(--unit));
  left: calc(50% - (var(--unit)));
  border: var(--unit) solid transparent;
  border-top-color: inherit;
}
.gameboard li:last-child::before,
.gameboard li:last-child::after {
  content: none;
}
.gameboard li:nth-child(20n+1){
  --hue: 1;
}
.gameboard li:nth-child(20n+2){
  --hue: 2;
}
